<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 基础表格
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button
                    type="primary"
                    icon="el-icon-circle-plus"
                    class="handle-del mr10"
                    @click="handleAdd()"
                >新增员工
                </el-button>
                <el-button
                    type="primary"
                    icon="el-icon-delete"
                    class="handle-del mr10"
                    @click="delAllSelection"
                >批量删除
                </el-button>
                <el-input v-model="keyword" placeholder="请输入关键字" clearable prefix-icon="el-icon-search"
                          class="handle-input"></el-input>
                <el-button type="primary" @click="getData()">搜索</el-button>
                <el-button type="primary" @click="exports()">导出</el-button>
            </div>
            <el-table
                :data="tableData"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="username" label="用户名" align="center"></el-table-column>
                <el-table-column prop="password" label="密码" align="center">{{'***'}}</el-table-column>
                <el-table-column label="工号" align="center">
                    <template slot-scope="scope">{{ scope.row.workId }}</template>
                </el-table-column>
                <el-table-column label="全名" align="center" prop="fullname"></el-table-column>
                <el-table-column label="性别" align="center">
                    <template slot-scope="scope">{{ scope.row.employee.sex }}</template>
                </el-table-column>
                <el-table-column prop="gmtCreate" label="入职时间" width="200" align="center"></el-table-column>
                <el-table-column label="状态" align="center">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.employee.status==='在职'?'success':(scope.row.employee.status==='离职'?'danger':'')"
                        >{{ scope.row.employee.status }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleEdit(scope.$index, scope.row)"
                        >编辑
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            @click="handleDelete(scope.$index, scope.row)"
                        >删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="prev, pager, next ,->, total"
                    :page-count="pageTotal"
                    :current-page="query.pageIndex"
                    :page-size="query.size"
                    @current-change="handlePageChange"
                    @size-change="sizeChange"
                    @prev-click="handlePageChange"
                    @next-click="handlePageChange"
                ></el-pagination>
            </div>
        </div>

        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="用户名">
                    <el-input v-model="form.username"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="form.password"></el-input>
                </el-form-item>
                <el-form-item label="全名">
                    <el-input v-model="form.fullname"></el-input>
                </el-form-item>
                <el-form-item label="性别">
                    <el-input v-model="form.employee.sex"></el-input>
                </el-form-item>
                <el-form-item label="民族">
                    <el-input v-model="form.employee.nation"></el-input>
                </el-form-item>
                <el-form-item label="联系电话">
                    <el-input v-model="form.employee.telephone"></el-input>
                </el-form-item>
                <el-form-item label="地址">
                    <el-input v-model="form.employee.address"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 新增弹出框 -->
        <el-dialog title="新增员工" :visible.sync="addVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="用户名">
                    <el-input v-model="form.username"></el-input>
                </el-form-item>
                <el-form-item label="工号">
                    <el-input v-model="form.workId"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="form.password"></el-input>
                </el-form-item>
                <el-form-item label="全名">
                    <el-input v-model="form.fullname"></el-input>
                </el-form-item>
                <el-form-item label="性别">
                    <el-input v-model="form.employee.sex"></el-input>
                </el-form-item>
                <el-form-item label="民族">
                    <el-input v-model="form.employee.nation"></el-input>
                </el-form-item>
                <el-form-item label="身份证号">
                    <el-input v-model="form.employee.noId"></el-input>
                </el-form-item>
                <el-form-item label="联系电话">
                    <el-input v-model="form.employee.telephone"></el-input>
                </el-form-item>
                <el-form-item label="地址">
                    <el-input v-model="form.employee.address"></el-input>
                </el-form-item>
                <el-form-item label="地址">
                    <el-input v-model="form.employee.money"></el-input>
                </el-form-item>
                <el-form-item label="角色类型">
                    <el-select v-model="form.roleId" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveAdd()">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {getRequest} from "../../util/api";

export default {
    name: "infoList",
    data() {
        return {
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                size: 0
            },
            keyword: null,
            tableData: [],
            multipleSelection: [],
            delList: [],
            editVisible: false,
            addVisible: false,
            pageTotal: 0,
            total: 0,
            size: 5,
            page: 1,
            form: {
                username: "",
                password: "",
                workId: "",
                fullname: "",
                roleId: "",
                status: "",
                employee: {
                    fullname: "",
                    workId: "",
                    sex: "",
                    noId: "",
                    nation: "",
                    telephone: "",
                    address: "",
                    money: "",
                    status: ""
                }
            },
            idx: -1,
            id: -1,
            options: [{
                value: '001',
                label: '经理'
            }, {
                value: '002',
                label: '人事部门主管'
            }, {
                value: '003',
                label: '销售部门主管'
            }, {
                value: '004',
                label: '财务部门主管'
            }, {
                value: '005',
                label: '美术部门主管'
            },{
                value: '006',
                label: '策划部门主管'
            },{
                value: '007',
                label: '人事部门员工'
            },{
                value: '008',
                label: '销售部门员工'
            },{
                value: '009',
                label: '财务部门员工'
            },{
                value: '010',
                label: '美术部门员工'
            },{
                value: '011',
                label: '策划部门员工'
            }],
            value: ''
        };
    },
    mounted() {
        this.getData();
    },
    methods: {
        // 获取 easy-mock 的模拟数据
        getData() {
            console.log(this.$store.state.user.roleId)
            let url = 'oasys-back-end/getUser?page=' + this.page + '&size=' + this.size + '&fatherRole=' + this.$store.state.user.roleId;
            if (this.keyword != null) {
                console.log("keyword="+this.keyword)
                url += '&keyword=' + this.keyword
            }
            getRequest(url).then(resp => {
                this.tableData = resp.data.data;
                console.log(this.tableData)
                this.total = resp.data.total;
                this.pageTotal = Math.ceil(this.total / this.size)
            })
        },
        // 删除操作
        handleDelete(index, row) {
            // 二次确认删除
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            }).then(() => {
                let employee = row
                this.deleteRequest('oasys-back-end/deleteUser', employee).then(resp => {
                    if (resp) {
                        this.$message.success('删除成功');
                        this.tableData.splice(index, 1);
                    } else {
                        this.$message.error('删除失败');
                    }
                })
            })
                .catch(() => {
                });
        },
        // 多选操作
        handleSelectionChange(val) {
            this.multipleSelection = val;
            //console.log(this.multipleSelection.form)
        },
        delAllSelection() {
            const length = this.multipleSelection.length;
            let str = '';
            this.delList = this.delList.concat(this.multipleSelection);
            for (let i = 0; i < length; i++) {
                console.log(this.delList[i].workId)
                this.deleteRequest('oasys-back-end/deleteUser', this.delList[i]).then(resp => {
                    if (resp) {
                        str += this.delList[i].workId + ' ';
                    }
                })
                this.$message.error(`删除了` + str);
                this.multipleSelection = [];
                this.getData()
            }


        },
        // 编辑操作
        handleEdit(index, row) {
            this.idx = index;
            this.form = row;
            this.editVisible = true;
        },
        // 保存编辑
        saveEdit() {
            this.editVisible = false;
            this.$message.success(`修改第 ${this.idx + 1} 行成功`);
            this.postRequest('oasys-back-end/editUser', this.form).then(resp => {
                if (resp) {
                    this.$set(this.tableData, this.idx, this.form);
                    this.$message("修改成功");
                    this.getData();
                } else {
                    this.$message("修改失败");
                    this.getData();
                }
            })
        },
        // 新增操作
        handleAdd() {
            this.addVisible = true;
        },
        // 保存新增
        saveAdd() {
            this.addVisible = false;
            this.form.employee.workId = this.form.workId;
            console.log(this.form.employee.workId)
            this.form.employee.fullname = this.form.fullname;
            this.postRequest('oasys-back-end/addUser', this.form).then(resp => {
                if (resp) {
                    this.$message("新增成功");
                    this.getData();
                } else {
                    this.$message("新增失败");
                    this.getData();
                }
            })

        },
        // 分页导航
        handlePageChange(currentPage) {
            this.page = currentPage;
            this.getData();
        },
        sizeChange(currentSize) {
            this.size = currentSize;
            this.getData();
        },

        //导出操作
        exports: function () {
            window.open("oasys-back-end/export?fatherRole="+this.$store.state.user.roleId+"&keyword="+this.keyword);
        }
    }
}
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 300px;
    display: inline-block;
}

.table {
    width: 100%;
    font-size: 14px;
}

.red {
    color: #ff0000;
}

.mr10 {
    margin-right: 10px;
}

.table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
}
</style>



